.publication {
  padding-top: 30px;
  margin: auto;
  text-align: center;
  .line-public {
    width: 100%;
    height: 1px;
    background-color: #dbdbdb;
    @media screen and (max-width: 735px) {
      background-color: transparent;
    }
  }
  @media screen and (max-width: 735px) {
    padding-top: 0px;
  }
}

.but-wrapper {
  display: inline-block;
  @media screen and (max-width: 735px) {
    width: 100%;
    display: flex;
    border-bottom: 1px solid rgb(218, 218, 218);
    margin-bottom: 2px;
  }
  .but-public {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 18px 0;
    border-top: 1px solid #000;
    margin-top: -1px;
    @media screen and (max-width: 735px) {
      width: 33%;
    border-bottom: 2px solid #000;
    border-top: none;


    }
    p {
      font-size: 12px;
      font-weight: 600;
      padding-left: 8px;
      color: #262626;
      letter-spacing: 1px;
      margin: 0;
    }
  }
  .but-public2{
    border: none;
  }
}

.posts-line {
  display: flex;
  margin-bottom: 29px;
  @media screen and (max-width: 735px) {
    margin-bottom: 3px;
  }
  &:last-child {
    margin-bottom: 50px;
    @media screen and (max-width: 735px) {
      margin-bottom: 3px;
    }
  }
  .post {
    width: 290px;
    margin-right: 28px;
    @media screen and (max-width: 735px) {
      margin-right: 3px;
      width: 100%;
    }
    &:last-child {
      margin-right: 0px;
    }
    .post-wrapper {
      a {
        .post-img-cont {
          // height: 290px;
          // width: 290px;
          overflow: hidden;
          display: flex;
          align-items: center;
          max-width: 290px;
    max-height: 290px;
          @media screen and (max-width: 735px) {
            width: 100%;
          }
          img {
            max-width: 100%;
            min-height: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }
}

.publication-desktop {
  @media screen and (max-width: 735px) {
    display: none;
  }
}

.publication-mobile {
  display: none;
  @media screen and (max-width: 735px) {
    display: block;
  }
}
