.white-popup {
  position: relative;
  background: #fff;
  width: 100%;
  max-width: 816px;
  margin: auto;
  max-height: 600px;
  overflow: hidden;
}

.wrapper-popup-post {
  display: flex;
  .video-cont {
    height: 600px;
    width: 490px;
  }
  #player-post,
  #player-post2,
  #player-post3 {
    height: 600px;
    video {
      height: auto !important;
    }
  }
  .owl-dots {
    position: absolute;
    bottom: 15px;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    .owl-dot span {
      background-color: #fff;
      border: none;
      opacity: 0.5;
      height: 6px;
      width: 6px;
      margin-right: 4px;
    }
    .owl-dot.active span {
      background-color: #fff;
      border: none;
      opacity: 1;
    }
  }
  .owl-prev.disabled,
  .owl-next.disabled {
    opacity: 0;
  }
  .owl-prev {
    left: 10px;
    transform: rotate(180deg);
    width: 30px;
    img {
      width: 100%;
    }
  }
  .owl-next {
    right: 10px;
    width: 30px;
    img {
      width: 100%;
    }
  }
  .img-item-popup {
    width: 60%;
    background-color: #000000;
    height: 100%;
    .img-cont2 {
      height: 600px;
      background-color: #000000;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 490px !important;

      img {
        max-width: 100%;
        object-fit: cover;
      }
    }
  }
  .desc-item-popup {
    position: relative;
    width: 100%;
    .header-post {
      display: flex;
      align-items: center;
      padding: 16px;
      border-bottom: #e9e9e9 1px solid;
      .item-header {
        p {
          margin: 0;
          margin-left: 14px;
          font-size: 14px;
          font-weight: 600;
        }
        .geo {
          font-size: 12px;
          font-weight: 400;
          margin-top: 2px;
        }
        .border-img {
          border: 2px solid #b6b6b6;
          display: inline-block;
          border-radius: 100%;
          padding: 2px;
          height: 32px;

          .img-cont {
            height: 32px;

            img {
              height: 100%;
            }
          }
        }
      }
    }
    .footer-post {
      position: absolute;
      bottom: 0;
      // border-top: 1px solid #e9e9e9;
      padding: 0px 10px;
      box-sizing: border-box;
      width: 100%;
      .wrapper-footer-post {
        padding-top: 4px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .item-footer-post {
          display: flex;
          span {
            padding: 8px;
            display: flex;
          }
        }
      }
      .time-block {
        padding-bottom: 8px;
        padding-left: 8px;
        p {
          font-size: 10px;
          font-weight: 400;
          text-transform: uppercase;
          color: #8e8e8e;
        }
      }
    }
  }
}

.body-post {
  box-sizing: border-box;
  padding: 10px;
  overflow-y: scroll;
  position: relative;
  height: calc(600px - 84px - 73px);
  width: 100%;
  border-bottom: 1px solid #e9e9e9;
  p {
    font-size: 14px;
  }
}

.text-mob-post {
  p {
    font-size: 14px;
  }
}
