.wrapper-storis-popup {
  overflow: hidden;
  border-radius: 15px;
  margin: auto;
  position: relative;
  @media screen and (max-width: 735px) {
    border-radius: 0px;
    height: 100%;
    video {
      width: auto !important;
    }
  }
  video {
    &:hover {
      .right-storis {
        opacity: 1;
      }
    }
  }
}
#playerMob {
  height: 760px !important;
}

.storis-class.mfp-bg {
  background-color: #1a1a1a;
}

.white-popup-storis {
  max-height: initial;
  max-width: initial;
  width: initial;
  background: transparent;
}

#oframeplayer {
  background-color: #1a1a1a !important;
}

.right-storis,
.left-storis {
  position: absolute;
  top: 50%;
  width: 30px;
  margin-top: -30px;
  cursor: pointer;
  opacity: 0.2;
  transition: all 0.1s ease-in-out;
  &:hover {
    opacity: 1;
  }
  img {
    width: 100%;
  }
}

.left-storis {
  transform: rotate(180deg);
}

.mfp-close {
  @media screen and (max-width: 735px) {
    top: 0px;
  }
}
