.wrapper-profile {
  display: flex !important;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 30px 16px 0;
  @media screen and (max-width: 735px) {
    padding-bottom: 20px;
    padding-top: 0;
  }
}

.mobile-name{
  border-bottom: 1px solid rgb(231, 231, 231);
  padding: 15px 0;
  h2{
    text-align: center;
    font-size: 22px;
    font-weight: 700;
    margin: 0;
  }
}

.border-profile {
  border-radius: 100%;
  display: inline-block;
  border: 2px solid #dfdfdf;
  height: 150px;
  padding: 5px;
  @media screen and (max-width: 735px) {
    height: 77px;
    border: 1px solid #dfdfdf;
    padding: 3px;
  }
}

.item-profile {
  display: flex;
  @media screen and (max-width: 735px) {
    margin-right: 0px;
    align-items: center;
    margin-top: 20px;
  }
}

.item-descr {
  @media screen and (max-width: 735px) {
    margin-top: 10px;
  }
}

.img-cont {
  border-radius: 100%;
  overflow: hidden;
  display: inline-block;
  height: 150px;
  @media screen and (max-width: 735px) {
    height: 77px;
  }
  img {
    height: 100%;
  }
}

.line1 {
  display: flex;
  align-items: center;
  @media screen and (max-width: 735px) {
    margin-left: 0px;
    margin-top: 20px;
  }
  h2 {
    font-size: 28px;
    font-weight: 300;
    @media screen and (max-width: 735px) {
      margin: 0;
      margin-bottom: 12px;
    }
  }
}

.but-follow {
  margin-left: 20px;
  background-color: #0095f6;
  padding: 7px 25px;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  border-radius: 3px;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -khtml-user-select: none; /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  @media screen and (max-width: 735px) {
    margin-left: 0;
    padding: 7px 7px;
    width: 33%;
    text-align: center;
    margin-right: 10px;

  }
}

.but-follow2 {
  margin-left: 20px;
  background-color: #fff;
  padding: 7px 25px;
  color: rgb(0, 0, 0);
  font-size: 14px;
  font-weight: 600;
  border: 1px solid #dfdfdf;
  cursor: pointer;
  border-radius: 3px;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -khtml-user-select: none; /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  @media screen and (max-width: 735px) {
    margin-left: 0;
    padding: 7px 7px;
    width: 33%;

    text-align: center;
    margin-right: 10px;
    &:last-child{
    margin-right: 0px;

    }
  }
}

.but-follow.active {
  background-color: #ffffff00;
  color: #262626;
  border: 1px solid #dfdfdf;
}

.strong-desc {
  font-weight: 600;

  line-height: 23px;
}

.line2 {
  ul {
    display: flex;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    
    @media screen and (max-width: 735px) {
      margin-bottom: 0px;
      margin-left: 20px;
    }
    li {
      list-style-type: none;
      font-size: 16px;
      margin-right: 40px;
      font-weight: 400;
      @media screen and (max-width: 735px) {
        text-align: center;
        font-size: 12px;
        margin-right: 20px;
        font-weight: 400;
      }
      span {
        font-weight: 600;
        @media screen and (max-width: 735px) {
          font-size: 18px;
        font-weight: 700;
          
        }
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.line3 {
  color: #262626;
  p {
    margin: 0;
    font-size: 16px;
    line-height: 23px;
  }
  a {
    color: #00376b;
    white-space: nowrap;
    font-weight: 600;
    text-decoration: none;
    line-height: 23px;
  }
}
