.owl-carousel {
    // Styling Next and Prev buttons
    position: relative;
    z-index: 9999;
    box-sizing: border-box;
	.owl-nav {
		margin-top: 10px;
		text-align: center;
        -webkit-tap-highlight-color: transparent;
        z-index: 9999;

		[class*='owl-'] {
			color: #6dbc2f;
			font-size: 50px;
			padding: 20px;
			background: gray;
			display: inline-block;
			cursor: pointer;
			border-radius: 3px;

			&:hover {
				color:#61a82a;
				text-decoration: none;
			}
		}
		.disabled {
			opacity: 1;
			cursor: default;
        }
	}

	// Styling dots
	.owl-nav.disabled + .owl-dots {
		margin-top: 10px;
	}

	.owl-dots {
		text-align: center;
		-webkit-tap-highlight-color: transparent;

		.owl-dot {
			display: inline-block;
			zoom: 1;
			*display: inline;

			span {
				width: 15px;
                height: 15px;
                margin-right: 10px;
				background: #fff;
				display: block;
				-webkit-backface-visibility: visible;
				transition: opacity 200ms ease;
                border-radius: 50px;
                border: 1px solid #6dbc2f;
			}

			&.active,
			&:hover {
				span {
					background: #6dbc2f;
				}
			}
		}
	}
}

.owl-prev i, .owl-next i {
    color: #6dbc2f;
    font-size: 80px;
}
.owl-prev, .owl-next {
    position: absolute;
    top: 50%;
    margin-top: -30px;
}
.owl-prev {
    left: -50px;
}
.owl-next {
    right: -50px;
}