body {
  background-color: #fafafa;
  font-family: 'Segoe UI';
  width: 100%;
  position: relative;
  @media screen and (max-width: 735px) {
    background-color: #fff;
  }
}

.container {
  max-width: 935px;
  margin: auto;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  @media screen and (min-width: 736px) {
    padding: 0 20px;
  }
}

body.noscroll {
  overflow-y: hidden!important;
}

.mfp-wrap{
  overflow: hidden;
}

.mfp-container{
  padding: 0;
}

a{
  text-decoration: none !important;

}

.show{
  display: block !important;
}

button {
  border: none;
}

button:active,
button:focus {
  outline: none;
}
button::-moz-focus-inner {
  border: 0;
}

.mobile {
  display: none !important;
  @media screen and (max-width: 735px) {
    display: block !important;
  }
}

.desktop {
  display: block;
  @media screen and (max-width: 735px) {
    display: none !important;
  }
}

.right-new {
  right: -30px;
  position: absolute;
  top: 50%;
  width: 15px;
  opacity: 0.6;
  z-index: 1046;
  cursor: pointer;
  svg {
    width: 100%;
    height: 100%;
  }
}
