.wrapper-storis {
  display: flex !important;
  justify-content: space-between;
  padding-top: 44px;
}

.item-storis {
  cursor: pointer;
  width: 125px;
  @media screen and (max-width: 735px) {
    width: 72px;
  }

  .padding-cont-storis {
    padding: 10px 15px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    flex-direction: column;
    @media screen and (max-width: 735px) {
      padding: 0;
    }
  }
}

.storis-head {
  box-sizing: border-box;
  .border-storis {
    border: 1px solid #dfdfdf;
    border-radius: 100%;
    padding: 3px;
    @media screen and (max-width: 735px) {
      padding: 2px;
    }
    .img-storis {
      width: 77px;
      height: 77px;
      border-radius: 100%;
      overflow: hidden;
      margin: auto;
      @media screen and (max-width: 735px) {
        width: 56px;
        height: 56px;
      }
      img {
        width: 100%;
        height: 100%;
        display: block;
      }
    }
  }
}

.storis-title {
  font-size: 14px;
  font-weight: 600;
  color: #000;
  text-decoration: none !important;
  
  @media screen and (max-width: 735px) {
    font-size: 12px;
    font-weight: 400;
  }
}

.mob-info {
  margin-top: 20px;
  ul {
    padding: 12px 10px;
    border-top: 1px solid rgb(219, 219, 219);
    display: flex;
    justify-content: space-around;
    margin: 0;
    li {
      text-align: center;
      font-size: 14px;
      font-weight: 400;
      color: #8e8e8e;
      span {
        font-size: 14px;
        font-weight: 600;
        color: #262626;
      }
    }
  }
}
