.white-popup-mob {
  overflow: initial;
  max-width: initial;
  max-height: initial;
  .header-posts {
    position: fixed;
    width: 100%;
    height: 44px;
    padding: 0 16px;
    top: 0;
    left: 0;
    z-index: 99999;
    background-color: #fff;
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgb(219, 219, 219);
    justify-content: center;
    box-sizing: border-box;
    .close-posts-mob {
      position: absolute;
    }
    p {
      text-align: center;
      color: rgb(38, 38, 38);
      font-style: 16px;
      font-weight: 600;
      margin: 0;
    }
  }
  .wrapper-popup-post {
    display: block;
    .img-item-popup {
      width: 100%;
      background-color: #fff;
      .owl-nav {
        margin-top: 0;
      }
      .img-cont2 {
        width: 100% !important;
        height: 100%;
      }
    }
  }
  .desc-item-popup {
    .header-post {
      display: flex;
      align-items: center;
      padding: 16px;
      border-bottom: #e9e9e9 1px solid;
      padding: 10px;
      .item-header {
        p {
          margin: 0;
          margin-left: 14px;
          font-size: 14px;
          font-weight: 600;
        }
        .geo {
          font-size: 12px;
          font-weight: 400;
          margin-top: 2px;
        }
        .border-img {
          border: 2px solid #b6b6b6;
          display: inline-block;
          border-radius: 100%;
          padding: 2px;
          height: 32px;

          .img-cont {
            height: 32px;

            img {
              height: 100%;
            }
          }
        }
      }
    }
    .footer-post {
      position: relative;
      border: none;
    }
  }
}

.posts-mobile {
  top: 0 !important;
  overflow: initial !important;
  .mfp-container {
    background-color: #fff;
    padding: 0;
  }
  .mfp-content {
    vertical-align: initial;
    padding-top: 44px;
    overflow: hidden;
  }
  .mfp-close {
    position: fixed;
    left: 10px;
    transform: rotate(-90deg);
    top: 0;
  }
}

.posts-mobile.mfp-bg {
  background-color: #fff;
}

.wrapper-popup-post {
  .video-cont-mob {
    height: 100%;
    min-height: 300px;
    video {
      object-fit: cover;
    }
  }
}

#player-post-mob,
#player-post-mob2,
#player-post-mob3 {
  height: 100%;
}

.post-container-mob {
  video {
    width: auto !important;
  }
}
